*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
$roboto:'Roboto';
$cursive_font:'Satisfy';
$round:'Varela round';
// $cta:#fe8033;
$cta:#3a6b99;
// $bg:#272b34;
$bg:#1c202b;
// $light_bg:#2b3039;
$light_bg:#1c202b;
$text:#2d343e;
$white:#fff;

body{
    width: 100%;
    height: 100%;
    position: relative;
    font-family: $round;
}
.dFlex{
    display: flex;
    justify-content: center;
    align-items: center;
}
.container{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    padding:30px 50px 20px 50px;
    box-sizing: border-box;
    background-color: $bg;
}

nav{
    @extend .dFlex;
    justify-content: space-between;
    width: 100%;
    color:$white;
    .logo{
        font-size: 30px;
        text-transform: uppercase;
        color: rgba($white,.7);
    }
    ul{
        @extend .dFlex;
        margin: 0;
        padding: 0;
        list-style-type: none;
        li{
            font-size: 14px;
            margin-left:15px;
            padding:10px;
            box-sizing: border-box;
            color: rgba($white,.7);
            cursor: pointer;
        }
    }
    .search{
        color: rgba($white,.7);
        .fa{
            cursor: pointer;
            +.fa{
                margin-left: 15px;
            }
        }
    }
}
// main content

.main-content{
@extend .dFlex;
position: relative;
width: 70%;
height: 100%;
margin: 50px auto;
padding: 50px 0 0;
box-sizing: border-box;
justify-content: space-between;
flex-wrap: wrap;
h3{
    position: absolute;
    left: 0;
    top: 0;
    color:$text;
    font-size: 7.5vw;
}
.card{
    width: 30%;
    border-radius: 15px;
    @extend .dFlex;
    flex-direction: column;
    transform: scale(.8);
    transition: all .3s ease-in-out;
    
    margin-bottom: 50px;
}
.card img{ 
    margin-top:-5vw;
}
img{
    width: 200px;
}

.card_Header{
    @extend .dFlex;
    flex-direction: column;
    color: $white;
    margin:25px 0;
    h2{
        font-size: 18px;
    }
    p{
        font-size: 14px;
        text-align: center;
        color:rgba($white,.3);
        margin: 8px 0;
    }
    .price{
        font-size:25px;
        color:$cta;
        span{
          font-size:15px;
          display:inline-block;
          vertical-align: top;
        }
      }
    }
  }
  .card:hover{
    background-color:$light_bg;
    transform: scale(1.1);
    cursor:pointer;
  }
  .card:hover{
   .btn{
     background-color:$cta;
     color:$white;
   }      
 }


.btn{
 width:130px;
 height:35px;
 font-size:14px;
 border-radius: 35px;
 background-color:$text;
 color:rgba($white, .5);
 @extend .dFlex;
 cursor:pointer;
 position:relative;
 transition:background-color .3s ease;
 overflow:hidden;
}

.footer{
 @extend .dFlex;
 width:100%;
 justify-content: space-between;
 margin-top:auto;
 color:rgba($white, .3);
 font-size:12px;
 .social{
   @extend .dFlex;
   .fa{
     width:50px;
     height:50px;
     border-radius: 50px;
     border:1px solid rgba($white, .3);
     @extend .dFlex;
     font-size:14px;
     color:rgba($white, .3);
     cursor:pointer;
     +.fa{
       margin-left:15px;
     }
   }
 }
}

// Media Query
@media(max-width:765px){
    ul{
        flex-direction: column;
    }
    .main-content{
        flex-direction: column;
    }
}